.section {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

div pre {
  text-decoration: none;
  color: #000;
  background: #ffc;
  display: block;
  height: auto;
  padding: 2mm;
  box-shadow: 5px 5px 7px rgba(33, 33, 33, 0.7);
  transform: rotate(-6deg);
  transition: transform 0.15s linear;
  max-width: 360px;
  margin: 30px 10px;
}

div pre:hover,
div pre:focus {
  box-shadow: 10px 10px 7px rgba(0, 0, 0, 0.7);
  transform: scale(1.02);
  position: relative;
  z-index: 5;
}

.company-name {
  font-weight: 600;
  padding-top: 20px;
  font-size: 21px;
  padding-bottom: 1px;
}

p {
  padding-bottom: 1px;
  margin-bottom: 2px;
  font-size: 16px;
}

.bill {
  padding-top: 2px;
  letter-spacing: 1px;
}

.text-center {
  text-align: center;
}

.text-right {
  text-align: right;
}

td {
  padding: 5px 0;
}

.dark-background {
  background-color: #555;
}

.sub-total td {
  color: #ffffff;
}

.pad-l-5 {
  padding-left: 5px;
}

.info-total-price {
  font-weight: 500;
}

.total td:last-child {
  font-size: 21px;
}

.info-graditude-text {
  width: auto;
  margin: 10px 0;
}

.info-graditude-text h1 {
  font-weight: 600;
  padding-top: 20px;
  font-size: 21px;
  padding-bottom: 8px;
}

.border-bottom {
  border-bottom: 0.5px solid #ebebeb;
}

.table1 tr:last-child td {
  padding-top: 20px;
}

.separator {
  border-top: 2px dotted #000;
  border-bottom: 2px dotted #000; /* 1px de ancho, punteado, color negro */
  width: 100%;
}
